.allFilter{
    display: grid;
    grid-template-columns: 100px  1fr;
    max-width: 600px;
    margin: 1rem auto;
    justify-content: center;
    align-items: center;
    padding: 2rem;
}
.allFilter h2{
    background: crimson;
    font-size: 3rem;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    border-radius: 12px;
    color: #fff;
}
.filter-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.SearchBox,
.genderSelect,
.countrySelect
{
   margin-bottom: 5px;
   width: 100%;
   padding: 0.5rem;
   border: none;
   outline: none;
   font-size: 1.5rem;
   border-radius: 12px;
}

