.card-container{
    background: rgba(253, 253, 253, 0.796);
    overflow: hidden;
    border-radius: 15px;
    position: relative;
    transition: all 0.5s linear;
    box-shadow: 3px 5px 2px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    position: relative;
}

.card-container:hover{
    transform: scale(1.05);
}
.card-container img{
    width: 100%;
}

.card-container .country{
    position: absolute;
    top: 10px;
    left: 5px;
    border-radius: 10px;
    margin: 0;
    background: rgb(146, 0, 32);
    color: #fff;
    padding: 0.1rem 0.3rem;
}