.Popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 300;
  background: #fff;
  box-shadow: 3px 5px 2px rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  padding: 1rem;
}
.Popup img {
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid crimson;
  box-shadow: 3px 5px 2px rgba(0, 0, 0, 0.2);
  position: absolute;
  top: -95px;
  left: 50%;
  transform: translateX(-50%);
}
.title {
  color: crimson;
}

.single {
  text-align: justify;
}
