.cards-container{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(250px,250px));
    padding: 2rem;
    max-width: 1170px;
    margin: 1rem auto;
    gap: 20px;
   justify-content: center;
   align-items: center;
}

.cards-container .counter{
    position: absolute;
    top: 0;
    left: 20px;
    font-size: 3rem;
    background: rgb(87, 0, 87);
    width: 80px;
    height: 80px;
    border-radius: 12px;
    color: #fff;
    display: grid;
    place-items: center;
    box-shadow: 3px 5px 2px rgba(0, 0, 0, 0.2);
}