.overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    z-index: 100;
}

.overlay .closeBtn{
    position: fixed;
    top: 10px;
    right: 20px;
    cursor: pointer;
    border: none;
    outline: none;
    background: rgb(218, 28, 28);
    font-size: 1.5rem;
    padding: 0.5rem;
    border-radius: 12px;
    text-transform: capitalize;
    transition: all 0.5s linear;
}

.overlay .closeBtn:hover{
    transform:rotate(360deg);
}